body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "Lobster";
  font-style: normal;
  font-weight: 400;
  src: url(/redux-picSaver/static/media/Lobster.2d942223.ttf) format("truetype"); /* Safari, Android, iOS */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heart_black {
  background: url(/redux-picSaver/static/media/heart.6c06f3e7.svg);
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  display: inline-block;
}
.heart_red {
  background: url(/redux-picSaver/static/media/heart_red.c4cfb5ca.svg);
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  display: inline-block;
}
.menu {
  background: url(/redux-picSaver/static/media/menu.14c32a6d.svg);
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  display: inline-block;
}
.close {
  background: url(/redux-picSaver/static/media/close.bc4634b5.svg);
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  display: inline-block;
}

.header {
  text-align: center;
  border-bottom: 1px solid #cccbcb;
  height: 50px;
}
.header__title {
  font-family: Lobster;
  margin: 0;
  font-size: 30px;
  margin: 10px;
  color: #42648b;
}

.sidemenu {
  position: fixed;
  left: 0;
  height: 100%;
  top: 0;
  width: 100%;
  background: white;
  box-shadow: 0px 0px 5px 5px rgba(26, 25, 25, 0.151);
  z-index: 1000;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.sidemenu_display_hidden {
  left: -120%;
}
.sidemenu__items {
  width: 100%;
  display: block;
  margin: auto;
  margin-left: -40px;
  text-align: center;
  list-style-type: none;
  position: absolute;
  top: 10%;
}
.sidemenu__items li {
  text-transform: uppercase;
  margin: 10px;
  font-size: 30px;
  font-weight: bold;
}
.sidemenu__items li a {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  text-decoration: none;
  color: #000000;
}
.sidemenu__items li a:active {
  color: #000000;
}
.sidemenu__items li a:hover {
  font-size: 35px;
}
.sidemenu__avatar-block {
  text-align: center;
  color: #6b6b6b;
}
.sidemenu__avatar-block p {
  font-weight: normal;
  font-size: 15px;
}
.sidemenu__avatar-block img {
  border-radius: 100%;
  max-width: 100px;
}

.search {
  text-align: center;
  padding: 40px;
}
.search__input {
  width: 70%;
  display: inline-block;
  padding: 15px;
  border-radius: 5px;
  border: 3px solid #42648b;
  font-size: 20px;
}

.warning-block {
  text-align: center;
  text-transform: uppercase;
  color: rgb(165, 165, 165);
}

.photos__error {
  text-align: center;
  margin: auto;
  width: 300px;
}
.photos__show-more-btn {
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  text-align: center;
  display: block;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
  color: rgba(66, 100, 142, 0.8);
}
.photos__show-more-btn:hover {
  font-size: 17px;
  color: rgba(66, 100, 142, 1);
}
.photos__show-more-block {
  text-align: center;
  padding-bottom: 20px;
}
.photos__show-more-block .spinner2 {
  margin: auto;
}

.spinner {
  width: 40px;
  height: 40px;

  position: relative;
  margin: 100px auto;
}

.double-bounce1,
.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #333;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;

  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

.photos__item {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  display: block;
  max-width: 300px;
  height: 300px;
  margin: auto;
  margin-bottom: 30px;
  padding: 10px;
  border: 1px solid #c0bdbd;
  border-radius: 3px;
}
.photos__item:hover {
  box-shadow: 0px 10px 10px 1px #c0bdbd;
}
.photos__item_warapper {
  height: 90%;
  overflow: hidden;
}
.photos__item_img {
}
.photos__item_footer {
  margin-top: 15px;
}
.photos__item_footer span:first-child {
  float: left;
}
.photos__item_footer span:nth-child(2) {
  float: right;
}

.spinner2 {
  width: 50px;
  height: 20px;
  text-align: center;
  font-size: 10px;
}

.spinner2 > div {
  background-color: #333;
  height: 100%;
  width: 6px;
  display: inline-block;
  margin: 1px;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.spinner2 .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner2 .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner2 .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner2 .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@-webkit-keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}

.favorite__header {
  text-align: center;
  text-transform: uppercase;
  color: rgb(165, 165, 165);
}
.favorites__body {
  margin: auto;
  width: 90%;
  text-align: center;
}
.favorites__body .photos__item {
  display: inline-block;
  margin-bottom: 0;
  margin: 5px;
  max-width: 200px;
  max-height: 200px;
}
.favorites__body .photos__item_warapper {
  height: 85%;
}
.favorites__body .photos__item_footer .heart_red {
  width: 15px;
  height: 15px;
  background-size: 15px;
}
.favorites__body .photos__item_footer .heart_black {
  width: 15px;
  height: 15px;
  background-size: 15px;
}

.no-match {
  text-align: center;
  font-size: 40px;
}

.entrance {
  box-shadow: 0px 0px 10px 0px #b7b7b7;
  width: 50%;
  padding: 20px;
  margin: auto;
  text-align: center;
  margin-top: 10%;
  border-radius: 2px;
}
.entrance__title {
  font-size: 25px;
  color: rgb(104, 104, 104);
}
.entrance__avatar {
  border-radius: 100%;
  max-width: 100px;
}
.entrance__btn {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 7px;
  padding-bottom: 7px;
  border: 0px;
  background: #42648b;
  font-size: 15px;
  color: #fff;
  border-radius: 4px;
  font-weight: bold;
}
.entrance__btn:hover {
  background: #5181b8;
}

